.section{
    position: absolute;
    width: 100%;
    height: auto;
    padding: 0 50px;
    bottom: 20px;
    color: white;
} 
.section h4{
    font-size: 22px;
}
.cards{
    position: relative;
    min-height: 200px;
    min-width: 170px;
    /* border: 1px solid white; */
    margin-top: 10px;
    display: flex;
    align-items: center;
    overflow-x: auto;
    scroll-behavior: smooth;
}
.cards::-webkit-scrollbar{
    display: none;
}
.cards .card{
    position: relative;
    min-width: 95%;
    height: 200px;
    /* border: 1px solid white; */
    border-radius: 8px;
    background-color: black;
    transition: .3s linear;
}
.cards .card .poster{
    width: 100%;
    height: 100%;
    border-radius: 8px;
    position: relative;
}
.cards .card:hover{
    /* min-width: 300px; */
    overflow: hidden;
}
.cards .card .rest_card{
    width: 100%;
    height: 100%;
    border-radius: 8px;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 999999;
    opacity: 0;
    transition: .5s linear;
}
.cards .card .rest_card img{
    width: 100%;
    height: 100%;
    border-radius: 8px;
    position: relative;
    z-index: -1;
}
.cards .card .rest_card::after{
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    background: linear-gradient(180deg, transparent, black);
    z-index: -1;
    left: 0;
    bottom: 0;
}
.cards .card .rest_card .cont{
    position: absolute;
    width: 100%;
    bottom: 10px;
    left: 0;
    color: white;
    padding: 0 20px;
}
/* .cards .card .rest_card .cont .sub{
    display: flex;
    align-items: center;
    justify-content: space-between;
} */
.cards .card .rest_card .cont .sub p{
    font-size: 12px;
    font-weight: 400;
    color: rgb(255, 255, 255, .7);
}
.cards .card .rest_card .cont .sub h3{
    color: white;
    font-size: 12px;
    display: flex;
    margin-top: 5px;
    align-items: center;
}
.cards .card .rest_card .cont .sub h3 p{
    margin: 5px 0 0 3px;
}
.cards .card .rest_card .cont .sub h3 span{
    background: yellow;
    color: black;
    padding: 0 3px;
    margin-top: 5px;
    font-weight: 500;
    border-radius: 2px;
}
.cards .card .rest_card .cont .sub h3 i{
    margin: 5px 0 0 12px;
}
.cards .card:hover .rest_card{
    opacity: 1;
}
.fa-chevron-left,
.fa-chevron-right{
    position: absolute;
    top: 50%;
    left: 3%;
    width: 25px;
    height: 25px;
    background: rgb(235, 235, 235, .3);
    color: white;
    display: grid;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    font-size: 12px;
    cursor: pointer;
    transition: .3s linear;
    z-index: 9999999;
    opacity: 0;
}
.section .fa-chevron-right{
    left: unset;
    right: 3%;
}
.section:hover .fa-chevron-right{
    opacity: 1;
}
.section:hover .fa-chevron-left{
    opacity: 1;
}