*{
    margin: 0;
    padding: 0;
    font-family: 'Poppins', sans-serif;
    box-sizing: border-box;
}

.header1{
    width: 100%;
    height: 100vh;
    /* background-image: linear-gradient(rgb(0,0,0,0.7), rgb(0,0,0,0.7)), url(/public/photos/background3.jpg); */
    background-image: url(/public/photos/background3.jpg);
    background-size: cover;
    background-position: center;
    padding: 10px 8%;
    position: relative;
}
.nav{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 0;
}
.logo{
    width: 150px;
    cursor: pointer;
}
.nav button{
    border: 0;
    outline: 0;
    background: #db0001;
    color: #fff;
    padding: 7px 20px;
    font-size: 12px;
    border-radius: 4px;
    margin-left: 10px;
    cursor: pointer;
}
.nav .button{
    display: inline-flex;
    align-items: center;
    background: transparent;
    border: 1px solid #fff;
    padding: 7px 10px;
}
.button img{
    width: 10px;
    margin-left: 10px;
}
.content1{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    margin-top: 150px;
}
.content1 h1{
    font-size: 60px;
    line-height: 70px;
    font-weight: 600;
    max-width: 650px;
    color: white;
}
.content1 h3{
    font-weight: 400;
    margin-bottom: 20px;
    color: white;
}
.content1 p{
    color: white;
}
.email-signup{
    /* background: #fff; */
    border-radius: 4px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 30px;
    overflow: hidden;
} 
.email-signup input{
    flex: 1;
    border: 0;
    outline: 0;
    margin-left: 20px;
}
.email-signup button{
    background: #db0001;
    border: 0;
    outline: 0;
    color: white;
    font-size: 20px;
    cursor: pointer;
    padding: 18px 33px;
} 

form p{
    margin: 0;
    padding: 0;
    color: red;
    font-size: 12px;
}