.header{
    position: relative;
    width: 100%;
    min-height: 100vh;
    /* border: 1px solid white; */
    overflow: hidden;
    box-shadow: 0px 10px 25px -8px #000;
}
.header::before{
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    background: url(/public/photos/img/money\ heist1.jpg) no-repeat center center/cover;
    opacity: 0.9;
    z-index: -1;
}
.header::after{
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    background: linear-gradient(180deg, transparent, #000);
    z-index: -1;
}
.header video{
    position: absolute;
    width: 120%;
    height: 190%;
    top: -47%;
    z-index: -1;
}
.header nav{
    width: 100%;
    height: 10%;
    /* border: 1px solid white; */
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 30px;
}
.header nav .logo_ul{
    display: flex;
    align-items: center;
    justify-content: center;
}
.header nav .logo_ul img{
    width: 120px;
    filter: contrast(1.75);
    margin-top: 5px;
}
.header nav .logo_ul ul{
    display: flex;
    align-items: center;
    justify-content: center;
    list-style: none;
}
.header nav .logo_ul ul li{
    padding: 3px 15px;
}
.header nav .logo_ul ul li a{
    text-decoration: none;
    color: white;
    font-size: 10px;
    font-weight: bold;
    transition: .3s linear;
}
.header nav .logo_ul ul li a:hover{
    color: rgb(235, 235, 235, .7);
}
.header nav .search_user{
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
}
.header nav .search_user input{
    padding: 3px 7px;
    border-radius: 20px;
    border: none;
    outline: none;
    background: rgb(235, 235, 235, .3);
    color: white;
    margin-right: 3px;
}
.header nav .search_user input::placeholder{
    color: white;
}
.header nav .search_user button{
  cursor: pointer;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  margin-right: 7px;
}
.header nav .search_user img{
    width: 20px;
    height: 20px;
    border-radius: 50%;
}
.header .content{
    position: relative;
    color: white;
    margin-top: 64px;
    padding-left: 50px;
}
.header .content h1{
   font-size: 50px;
   margin-bottom: 10px;
}
.header .content p{
    font-size: 13px;
    width: 300px;
    word-break: break-all;
}
.header .content .details{
    display: flex;
    align-items: center;
    margin-top: 10px;
}
.header .content .details h6{
    color: rgb(235, 235, 235, .8);
    font-size: 12px;
    margin-right: 10px;
}
.header .content .details h5{
    color: rgb(142, 230, 48);
    font-size: 12px;
    margin-right: 10px;
}
.header .content .details h4{
    color: white;
    font-size: 12px;
    margin-right: 10px;
}
.header .content .details h3{
    color: white;
    display: flex;
    align-items: center;
    font-size: 14px;
}
.header .content .details h3 span{
    background-color: yellow;
    color: black;
    padding: 0px 3px;
    margin:  0px 5px 0px 10px;
    font-weight: 500;
}
.header .content .details h3 i{
    margin-right: 5px;
}
.header .content .btns{
    margin-top: 20px;
    display: flex;
    align-items: center;
}
.header .content .btns a{
    padding: 8px 12px;
    font-size: 15px;
    font-weight: 600;
    background: linear-gradient(90deg, #00bdff, #0079ff);
    color: white;
    margin-left: 10px;
    text-decoration: none;
    animation: button 1s linear infinite;
}
@keyframes button {
    0%{
        background: linear-gradient(0deg, #00bdff, #0079ff); 
    }
    12%{
        background: linear-gradient(45deg, #00bdff, #0079ff);
    }
    24%{
        background: linear-gradient(95deg, #00bdff, #0079ff);
    }
    36%{
        background: linear-gradient(145deg, #00bdff, #0079ff);
    }
    48%{
        background: linear-gradient(195deg, #00bdff, #0079ff);
    }
    60%{
        background: linear-gradient(245deg, #00bdff, #0079ff);
    }
    72%{
        background: linear-gradient(295deg, #00bdff, #0079ff);
    }
    100%{
        background: linear-gradient(360deg, #00bdff, #0079ff);
    }
}
.header .section{
    position: absolute;
    width: 100%;
    height: auto;
    padding: 0 50px;
    bottom: 20px;
    color: white;
}
.header .section .cards{
    position: relative;
    width: 100%;
    height: 200px;
    /* border: 1px solid white; */
    margin-top: 10px;
    display: flex;
    align-items: center;
    overflow-x: auto;
    scroll-behavior: smooth;
}
.movies::-webkit-scrollbar{
    display: none;
}
